import "./SearchRide.css";
const today = new Date();
const numberOfDaysToAdd = 3;
const date = today.setDate(today.getDate() + numberOfDaysToAdd);

function SearchRide(){
    const defaultValue = new Date(date).toISOString().split('T')[0];
    return (
        <div className="search_ride">
            <div className="search_inp">
                <div className="ring">O</div>
                <input type="text" placeholder="Mumbai" className="search_inp_txt"/>
            </div>
            <div className="search_inp">
                <div className="ring">O</div>
                <input type="text" placeholder="Pune" className="search_inp_txt"/>
            </div>
            <div>
                <input type="date" defaultValue={defaultValue} className="search_date" />
            </div>
            
            <button className="search_button">Search</button>
            
        </div>
    )
}

export default SearchRide;