import "./RideCard.css";
import {useNavigate} from "react-router-dom";

function RideCard(){

    let nav = useNavigate();
    const gotoBookRide = ()=>{
        nav('/book-ride');
    };

    return(
        <div class="card" onClick={gotoBookRide}>
        <div class="ca-p-a">
            <div class="cap-1">
                <div class="cap-a">
                    <div class="dm">
                        <div class="d1">20:10</div>
                        <div class="d2">2h:10</div>
                    </div>
                    <div class="dm-g">
                        <i class='bx bxs-circle'></i>
                    </div>
                    <div class="dm-2">
                        Mumbai
                    </div>
                </div>
                <div class="cap-b" >
                    <div class="dm">
                        <div class="d1">22:30</div>
                    </div>
                    <div class="dm-g">
                        <i class='bx bxs-circle'></i>
                    </div>
                    <div class="dm-2">
                        Pune
                    </div>
                </div>
               
            </div>
            <div class="cap-2">
                ₹200.00
            </div>
        </div>
        <div class="ca-p-b">
            <div class="ic-11" >
                <div class="ic-1"><i class='bx bxs-user-circle'></i></div>
                <div class="ic-2">MARTIN NILE</div>
            </div>
            <div class="ic-3"><i class='bx bxs-info-circle'></i></div>
        </div>
    </div>
    );
}

export default RideCard;