import { useEffect, useState } from "react";
import logo from "../images/logo3.png";
import profilePic from "../images/profile_pic.png";
import searchLogo from "../images/search.png";
import "./TopBar.css";

import { auth } from '../../firebase-config';
import { onAuthStateChanged, signOut } from 'firebase/auth';
import { NavLink, useLocation, useNavigate } from "react-router-dom";
import { async } from "@firebase/util";


function TopBar() {

    const [user, setUser] = useState({});
    const [showSignOut, setShowSignOut] = useState(false);
    const location = useLocation();

    let nav = useNavigate();

    const navToLand = () => {
        nav('/');
    }

    onAuthStateChanged(auth, (currentUser) => {
        setUser(currentUser);
    });

    const logout = async () => {
        await signOut(auth);
    }

    const showSignOutHandler = () => {
        if (showSignOut === false) {
            setShowSignOut(true);
        } else {
            setShowSignOut(false);
        }
    }

    useEffect(() => {
        const hideSignOut = (e) => {

            if (e.target.id !== "user_name") {
                setShowSignOut(false);
            }
        }

        document.body.addEventListener('click', hideSignOut);
        return () => document.body.addEventListener('click', hideSignOut);
    }, [])


    return (
        <>
            <div className="top_bar">
                <img className="logo" onClick={navToLand} src={logo} alt="Logo" />
                <div className="user">
                    {location.pathname === "/search" ? <></> : <NavLink to={"/search"} className="search">
                        <img className="searchLogo" src={searchLogo} alt="O" />
                        <h4>Search</h4>
                    </NavLink>}
                    {user ? <NavLink to={"/pub"} className="pub_ride" id="pub_ride">Publish A Ride</NavLink> : <NavLink to={"/login"} className="pub_ride"><h4>Publish A Ride</h4></NavLink>}
                    {user && <img className="profile_pic" src={profilePic} alt="Profile Pic" />}
                    {user ? <h4 onClick={() => showSignOutHandler()} className="user_name" id="user_name">{user.email}</h4> : <NavLink to={"/login"} className="login_text"><h4>Login</h4></NavLink>}

                </div>
            </div>

            {showSignOut && <button onClick={logout} className="sign_out">
                <h4>Sign Out</h4>
            </button>}
        </>
    )
}

export default TopBar;